@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);

    * {
        box-sizing: border-box;
        margin: 0;
        padding: 0;
    }
    
     ::-webkit-scrollbar {
        width: 12px;
        height: 4px;
    }
    
     ::-webkit-scrollbar-track {
        box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
        -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
        border-radius: 0px;
    }
    
     ::-webkit-scrollbar-thumb {
        border-radius: 0px;
        background: rgba(255, 0, 0, 0.8);
        box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.5);
        -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.5);
    }
    
     ::-webkit-scrollbar-thumb:window-inactive {
        background: rgba(255, 0, 0, 0.4);
    }
.nav {
    z-index: 1;
    overflow: hidden;
    position: fixed;
    width: 100%;
    height: 40px;
    top: 0;
    left: 0;
    background-color: white;
    display: -webkit-flex;
    display: flex;
    border-bottom: 1px solid rgb(179, 179, 179);
}

.title {
    margin: auto;
    position: relative;
    top: 0;
    left: 0;
    font-family: 'Courier New', Courier, monospace;
    font-weight: bolder;
    font-size: xx-large;
    display: -webkit-flex;
    display: flex;
}

.bi-camera,
.bi-telegram {
    margin: 6px 5px 0px 7px;
    font-size: x-large;
    position: relative;
    top: 0;
    left: 0;
    display: -webkit-flex;
    display: flex;
    cursor: pointer;
}

.bi-telegram {
    color: rgb(0, 94, 255);
}
.story-main {
    overflow: auto;
    white-space: nowrap;
    margin-top: 40px;
    position: relative;
    scroll-behavior: smooth;
}

.story {
    margin: 10px 0 5px 0;
    display: -webkit-inline-flex;
    display: inline-flex;
    position: relative;
}

.bi1 {
    margin: 0 13px 0 13px;
    width: 60px;
    height: 60px;
    font-size: 40px;
    border: double 2px transparent;
    border-radius: 50%;
    background-image: linear-gradient(white, white), radial-gradient(circle at top left, #f00, #3020ff);
    background-origin: border-box;
    background-clip: content-box, border-box;
    position: relative;
    top: 0;
    left: 0;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    text-align: center;
}

ul {
    display: -webkit-inline-flex;
    display: inline-flex;
    list-style-type: none;
    position: relative;
    top: 0;
    left: 0px;
}

li {
    margin: 0 17px 5px 17px;
    width: 52px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    text-align: center;
    position: relative;
    top: 0;
    left: 0;
}
.post-main {
    position: relative;
    display: -webkit-flex;
    display: flex;
    margin: 20px;
    border: 2px solid gray;
    -webkit-flex-direction: column;
            flex-direction: column;
}

.post {
    width: 100%;
    position: relative;
    border-bottom: 1px gray solid;
}

.post-head {
    display: -webkit-flex;
    display: flex;
    border-bottom: 1px gray solid;
}

.bi2 {
    margin: 10px 0 10px 15px;
    font-size: x-large;
    display: -webkit-flex;
    display: flex;
    position: relative;
    background: #121FCF;
    background: linear-gradient(to right, #121FCF 0%, #CF1512 100%);
    -webkit-background-clip: text;
            background-clip: text;
    -webkit-text-fill-color: transparent;
}

.post-title {
    margin: 3px 0 0 8px;
    font-size: 15px;
    font-weight: 600;
    font-family: 'Roboto', sans-serif;
    font-style: normal;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
}

.img {
    background-color: rgb(196, 192, 192);
    border-bottom: 1px black solid;
    margin-bottom: 10px;
}

.bi-images {
    position: relative;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    font-size: 250px;
    margin: 0 50px 0 50px;
    background-color: white;
    -webkit-flex-direction: column;
            flex-direction: column;
    border-bottom: 1px gray solid;
}

.img-text {
    display: -webkit-flex;
    display: flex;
    font-size: large;
}

.post-foot {
    display: -webkit-flex;
    display: flex;
}

input[type=checkbox] {
    display: none;
}

.bi3 {
    margin: 5px 5px 5px 7px;
    display: -webkit-flex;
    display: flex;
    position: relative;
    font-size: 22px;
}

.bi-suit-heart-fill {
    display: none;
}

.post-foot #like1:checked~label .h1,
.post-foot #like2:checked~label .h2,
.post-foot #like3:checked~label .h3,
.post-foot #like4:checked~label .h4,
.post-foot #like5:checked~label .h5,
.post-foot #like6:checked~label .h6,
.post-foot #like7:checked~label .h7,
.post-foot #like8:checked~label .h8,
.post-foot #like9:checked~label .h9,
.post-foot #like10:checked~label .h10 {
    display: none;
}

.post-foot #like1:checked~label .hf1,
.post-foot #like2:checked~label .hf2,
.post-foot #like3:checked~label .hf3,
.post-foot #like4:checked~label .hf4,
.post-foot #like5:checked~label .hf5,
.post-foot #like6:checked~label .hf6,
.post-foot #like7:checked~label .hf7,
.post-foot #like8:checked~label .hf8,
.post-foot #like9:checked~label .hf9,
.post-foot #like10:checked~label .hf10 {
    display: -webkit-flex;
    display: flex;
    color: #CF1512;
}

.bi-bookmark-check-fill {
    display: none;
}

.post-foot #bmark1:checked~label .b1,
.post-foot #bmark2:checked~label .b2,
.post-foot #bmark3:checked~label .b3,
.post-foot #bmark4:checked~label .b4,
.post-foot #bmark5:checked~label .b5,
.post-foot #bmark6:checked~label .b6,
.post-foot #bmark7:checked~label .b7,
.post-foot #bmark8:checked~label .b8,
.post-foot #bmark9:checked~label .b9,
.post-foot #bmark10:checked~label .b10 {
    display: none;
}

.post-foot #bmark1:checked~label .bf1,
.post-foot #bmark2:checked~label .bf2,
.post-foot #bmark3:checked~label .bf3,
.post-foot #bmark4:checked~label .bf4,
.post-foot #bmark5:checked~label .bf5,
.post-foot #bmark6:checked~label .bf6,
.post-foot #bmark7:checked~label .bf7,
.post-foot #bmark8:checked~label .bf8,
.post-foot #bmark9:checked~label .bf9,
.post-foot #bmark10:checked~label .bf10 {
    display: -webkit-flex;
    display: flex;
}

h5,
h6 {
    margin: 5px;
}

h6 {
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    font-weight: lighter;
    color: rgb(81, 80, 80);
}
.footer {
    overflow: hidden;
    position: fixed;
    bottom: 0;
    width: 100%;
    height: 40px;
    background-color: white;
    margin: auto;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    border-top: 2px solid gray;
}

.bi4 {
    font-size: xx-large;
    margin: 5px 25px 5px 25px;
}
