.nav {
    z-index: 1;
    overflow: hidden;
    position: fixed;
    width: 100%;
    height: 40px;
    top: 0;
    left: 0;
    background-color: white;
    display: flex;
    border-bottom: 1px solid rgb(179, 179, 179);
}

.title {
    margin: auto;
    position: relative;
    top: 0;
    left: 0;
    font-family: 'Courier New', Courier, monospace;
    font-weight: bolder;
    font-size: xx-large;
    display: flex;
}

.bi-camera,
.bi-telegram {
    margin: 6px 5px 0px 7px;
    font-size: x-large;
    position: relative;
    top: 0;
    left: 0;
    display: flex;
    cursor: pointer;
}

.bi-telegram {
    color: rgb(0, 94, 255);
}