.story-main {
    overflow: auto;
    white-space: nowrap;
    margin-top: 40px;
    position: relative;
    scroll-behavior: smooth;
}

.story {
    margin: 10px 0 5px 0;
    display: inline-flex;
    position: relative;
}

.bi1 {
    margin: 0 13px 0 13px;
    width: 60px;
    height: 60px;
    font-size: 40px;
    border: double 2px transparent;
    border-radius: 50%;
    background-image: linear-gradient(white, white), radial-gradient(circle at top left, #f00, #3020ff);
    background-origin: border-box;
    background-clip: content-box, border-box;
    position: relative;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
}

ul {
    display: inline-flex;
    list-style-type: none;
    position: relative;
    top: 0;
    left: 0px;
}

li {
    margin: 0 17px 5px 17px;
    width: 52px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    position: relative;
    top: 0;
    left: 0;
}